import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("/chadepanela/")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.luisjulliana.bridalshower.pages.Home() }
        ctx.router.register("/checkout") { org.luisjulliana.bridalshower.pages.Checkout() }
        ctx.router.register("/wishlist") { org.luisjulliana.bridalshower.pages.Wishlist() }

    }
    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(org.luisjulliana.bridalshower.components.styles.ItemScaleAnimation)
        ctx.theme.registerComponentStyle(org.luisjulliana.bridalshower.components.styles.DefaultFontStyle)
        ctx.theme.registerComponentStyle(org.luisjulliana.bridalshower.components.styles.LinkStyle)
        ctx.theme.registerComponentStyle(org.luisjulliana.bridalshower.components.styles.TitleStyle)
        ctx.theme.registerComponentStyle(org.luisjulliana.bridalshower.components.widgets.DefaultButtonStyle)
        ctx.theme.registerComponentVariants(org.luisjulliana.bridalshower.components.styles.GridStyleVariant)
        ctx.theme.registerComponentVariants(org.luisjulliana.bridalshower.components.styles.LargeTitleVariant)
        ctx.theme.registerComponentVariants(org.luisjulliana.bridalshower.components.styles.SubTitleStyle)
        ctx.stylesheet.registerKeyframes(org.luisjulliana.bridalshower.components.animations.scaleSize)
        org.luisjulliana.bridalshower.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Bridalshower")
        ) { org.luisjulliana.bridalshower.MyApp {
                com.varabyte.kobweb.silk.defer.renderWithDeferred { router.renderActivePage() }
            }
        }
    }
}
